@import url(https://fonts.googleapis.com/css2?family=Mulish&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

* {
  font-family: "Mulish", sans-serif !important;
}

.tab {
  font-size: 19px !important;
  padding: 15px !important;
}

.tab:hover {
  color: blue !important;
  text-decoration: underline;
}

.access {
  border-radius: 50px !important;
  font-size: 18px !important;
  width: 12rem !important;
  font-weight: 500 !important;
  border: 2px solid #2a79ad !important;
  transition: 0.5s;
  color: #2a79ad !important;
}

.access:hover {
  background-color: #2a79ad !important;
  color: #fff !important;
}

.main {
  margin-top: 5rem !important;
}

.started {
  border-radius: 2rem;
  border: 2px solid;
  background-color: rgba(0, 0, 255, 0.788);
  color: white;
  width: 12rem !important;
  height: 3rem;
  transition: 0.5s;
}
.started:hover {
  background-color: white;
  color: blue;
}

.head {
  font-weight: 1000 !important;
  font-size: 55px !important;
}
.watch {
  color: rgb(21, 45, 184);
  font-weight: 600;
  padding-top: 0.4rem !important;
  margin-left: 0.5rem !important;
}
.type {
  grid-template-columns: auto auto auto auto auto;
  display: grid;
}

.northern {
  padding-top: 2.5rem !important;
  margin-top: -0.6rem !important;
}

.google {
  margin-top: 0.5rem !important;
  margin-left: 0px !important;
}
.skrill {
  margin-top: 1.65rem !important;
  margin-left: -10px !important;
}

.item {
  /* padding: 20px; */
  margin-left: 5px;
}

p {
  font-size: 18px;
}

.video {
  display: grid;
  grid-template-columns: auto auto;
  padding: 1px;
}

.flex-box {
  margin-top: 0.5rem !important;
}
.flex-box:hover {
  cursor: pointer;
}

.header-img {
  text-align: right;
}

#header .animated {
  animation: up-down 2s ease-in-out infinite alternate-reverse both;
}

@-webkit-keyframes up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

@keyframes up-down {
  0% {
    -webkit-transform: translateY(10px);
            transform: translateY(10px);
  }
  100% {
    -webkit-transform: translateY(-10px);
            transform: translateY(-10px);
  }
}

.play-btn {
  width: 35px;
  height: 35px;
  background: radial-gradient(
    rgba(38, 0, 255, 0.8) 60%,
    rgb(38, 0, 255, 0.8) 62%
  );
  border-radius: 50%;
  position: relative;
  display: block;
}

/* triangle */
.play-btn::after {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translateX(-40%) translateY(-50%);
  transform: translateX(-40%) translateY(-50%);
  -webkit-transform-origin: center center;
          transform-origin: center center;
  width: 0;
  height: 0;
  border-top: 5px solid transparent;
  border-bottom: 5px solid transparent;
  border-left: 10px solid #fff;
  z-index: 100;
  transition: all 400ms cubic-bezier(0.55, 0.055, 0.675, 0.19);
}

.play-btn:hover {
  box-shadow: 0px 0px 20px 3px rgba(141, 140, 150, 0.8);
}

.navbar-brand {
  -webkit-animation: rotation 4s 1 linear;
          animation: rotation 4s 1 linear;
}

@-webkit-keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
  }
}

@keyframes rotation {
  from {
    -webkit-transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(359deg);
  }
}

.navbar-brand:hover {
  -webkit-animation: rotation 3s infinite linear;
          animation: rotation 3s infinite linear;
}

.navbar-toggler {
  background-color: rgb(104, 94, 94);
  color: white !important;
  font-size: 25px !important;
  width: 50px !important;
  padding-left: 10px !important;
  border-radius: 20% !important;
  transition: 0.3s;
}

.navbar-toggler:hover {
  box-shadow: 0px 0px 20px 3px rgba(152, 151, 161, 0.685);
}

.navbar-expand-lg {
  border: 1px solid #cdadad !important;
  background-color: #d5eafeb0 !important;
}

